import './Header.css'
import { Link } from "react-scroll";

const Header = () => {
    return (
		<div className = "main">
			<div className = "container-fluid">
				<div className = "row">
					<div className = "entry-banner text-center">
						<h1 id = "hello">Hello, I'm</h1>
						<h1 id = "name">XXXX</h1>
						<h1 id = "person-description">UI Developer. UX Desinger. Problem Solver.</h1>
					</div>
				</div>

				<div className = "row">
					<div className = "banner-buttons">
						<div className = "col">
						<Link smooth={true} to="about" className="btn btn-default btn-border" href="#">Info</Link>
						</div>
						<div className = "col">
						<Link smooth={true} to="projects" className="btn btn-default btn-border" href="#">Portfolio</Link>
						</div>
					</div>
				</div>
			</div>
		</div>

    );
}

export default Header;